import {
  ref,
  onMounted,
  onBeforeUnmount
} from "vue";
import {
  getNumInRange
} from "akaba-tool";

const stickyTop = 110;
const maxDeg = 30;

export function useRotateCard() {
  const cardElmRef = ref<HTMLElement | null>(null);
  const opacityValue = ref(1);
  const isFullShow = ref(false);
  let animateId = 0;
  let topDiff = 0;

  function onTopDiffChange(diffNum: number, detailInfo: { currentTop: number }) {
    if (cardElmRef.value) {
      const cardElm = cardElmRef.value; 
      const cardMainElm = cardElm.querySelector(".card-one-main") as HTMLDivElement;
      if (cardMainElm) {  
        if (diffNum <= 0) {
          const percent = (-diffNum)  / (cardElm.offsetHeight / 3 * 2);
          const xDeg = percent * maxDeg;
          let opacityNum = Math.round(percent * 100) / 100;
          opacityNum = getNumInRange(opacityNum, 0, 0.7);
          const scaleNum = getNumInRange(1 - opacityNum, 0.75, 1);
          cardMainElm.style.transform = `rotateX(${-xDeg}deg) scale(${scaleNum})`;
          opacityValue.value = opacityNum;
          if (isFullShow.value) {            
            isFullShow.value = false;
          }
          return;
        }
        cardMainElm.style.transform = "";
        opacityValue.value = 0;
        if (detailInfo.currentTop > (window.innerHeight - cardElm.offsetHeight)) {
          if (isFullShow.value) {
            isFullShow.value = false;
          }
          return;
        }
        if (!isFullShow.value) {            
          isFullShow.value = true;
        }
      }
    }
  }

  function loopWatchTop() {
    if (cardElmRef.value) {
      const cardElm = cardElmRef.value;    
      const currentTop = cardElm.getBoundingClientRect().top;
      const startReverseTop = stickyTop + cardElm.offsetHeight / 3 * 2;
      const currentTopDiff = currentTop - startReverseTop ;
      if (Math.abs(currentTopDiff - topDiff) > Number.EPSILON) {
        onTopDiffChange(currentTopDiff, { currentTop });
        topDiff = currentTopDiff;
      }
    }
    animateId = window.requestAnimationFrame(() => {
      loopWatchTop();
    });
  }
  
  onMounted(() => {
    //
    loopWatchTop();
  });

  onBeforeUnmount(() => {
    window.cancelAnimationFrame(animateId);
  });

  return {
    cardElmRef,
    opacityValue,
    isFullShow,
  };
}
